.wrapper {
  position: relative;
  width: 100%;

  display: flex;
  flex-direction: column;
  // min-height: 100vh;
  /* stylelint-disable-next-line */
  // min-height: calc(100 * var(--vh, 1vh));

  // .header,
  // .footer {
  //   flex-shrink: 0;
  // }

  main {
    flex-grow: 1;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

