.modal {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: $background-color-modal;

  &--close {
    display: none;
  }
}

.modal__wrapper {
  position: relative;
  margin: auto;
  padding: 40px;
  background-color: $modal-window-background;
  width: 26%;
}

.modal__wrapper h2 {
  font-family: $ff-roboto;
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: $color-default-white;
  margin: 0;
  margin-bottom: 15px;
}

.modal__wrapper p {
  margin: 0;
  margin-bottom: 40px;
  color: $color-default-white;
}

.modal__wrapper form {
  display: flex;
  flex-direction: column;
}

.modal__wrapper input {
  border: none;
  border-radius: 3px;
  background-color: $color-background-field;
  padding: 15px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  color: $color-default-white;
  font-size: 16px;

  &::placeholder {
    font-family: $ff-ptmono;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    opacity: 0.8;
  }
}

.modal__wrapper textarea {
  width: 100%;
  grid-column: 1/3;
  border: none;
  border-radius: 3px;
  background-color: $color-background-field;
  padding: 25px 15px;
  box-sizing: border-box;
  min-height: 155px;
  margin-bottom: 12px;

  &::placeholder {
    font-family: $ff-ptmono;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    opacity: 0.8;
  }
}

.modal__wrapper button {
  background-color: $color-dark-blue;
  padding: 15px 60px;
  width: 100%;
  font-family: $ff-ptmono;
  font-size: 16px;
  line-height: 19px;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 700;
  box-sizing: border-box;
  text-align: center;
}

.modal__wrapper label {
  &:last-of-type {
    position: relative;
    display: block;
    padding-left: 40px;
    order: 1;
  }
}

.modal__wrapper span {
  &:first-of-type {
    width: 15px;
    height: 15px;
    position: absolute;
    top: 40%;
    left: 12px;
    background-color: $color-light-blue;
    border: 1px solid $color-default-white;
    border-radius: 2px;
  }

  &:last-of-type {
    font-size: 14px;
    line-height: 16px;
    opacity: 0.8;
    vertical-align: middle;
    display: inline-block;
    margin-top: 15px;
    color: $color-default-white;
  }
}

.modal__wrapper input[type="checkbox"]:checked + span {
  &::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 3px;
    width: 8px;
    height: 5px;
    background-image: url("../../img/svg/check.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.modal__wrapper  .modal__close-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  background-image: url("../../img/svg/close-btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}
